import React from "react";
import {PdfViewer} from "./Viewer";
import * as pdfjsLib from "pdfjs-dist";
import printJS from "print-js";
import {PDFViewerMainClassPropsTypes, PDFViewerMainClassStateTypes} from "./types/PDFViewerTypes";
import {ArrowRepeat, Icons, MinusIcon, PlusIcon, PrintIcon} from "./Icons/Icons";
import './App.css';

export default class PDFViewer extends React.Component<PDFViewerMainClassPropsTypes, PDFViewerMainClassStateTypes> {
    //   ,  div'a    pdf 
    private mainDiv: React.RefObject<any>;

    constructor({ props }: { props: any }) {
        super(props);
        this.state = {
            numPages: 0, // . - 0
            numPagesArr: [1], //   .     
            scale: 1.5, // . - 1.5
            pdf: null, //  pdf 
            visibleMenu: false, //   ,     Drawer  AntD
            isRender: false, //   
            isLoadingError: false,
        }

        this.mainDiv = React.createRef<HTMLDivElement>(); // .    React.Ref .


        //    PDFViewer **     **
        pdfjsLib.GlobalWorkerOptions.workerSrc = `//unpkg.com/pdfjs-dist@${pdfjsLib.version}/legacy/build/pdf.worker.min.js`;
    }


    componentDidMount() {
        // cMapUrl    PDFViewer
        const cMapUrl = `//unpkg.com/pdfjs-dist@${pdfjsLib.version}/cmaps`
        const self = this;

        // pdf 
        const loadingTask = pdfjsLib.getDocument({url: this.props.link, cMapUrl: cMapUrl, cMapPacked: true})
        loadingTask.promise
            .then(loadedPdf => {
                //    ,
                //    JSX  map
                let numPagesArr:Array<number> = [];

                //     numPagesArr  
                for(let i = 1; i <= loadedPdf.numPages; i++){
                    numPagesArr.push(i)
                }

                // state  , :
                //pdf - 
                //numPages -  
                //numPagesArr -    
                this.setState({pdf: loadedPdf, numPages: loadedPdf.numPages, numPagesArr: numPagesArr}, () => {
                    //       state, (),     
                    // div         onWheelEvent
                    this.mainDiv.current?.addEventListener('wheel', (event: WheelEvent) => {
                        this.onWheelEvent(event)
                    })
                })
            }, function (reason) {
                self.setState({isLoadingError: true})
                console.error(reason);
            });
        loadingTask.promise.catch(error => {
            self.setState({isLoadingError: true})
            console.log(error)
        })
    }



    onWheelEvent = (event:WheelEvent) => {
        if(event.ctrlKey){
            event.preventDefault();
            if(event.deltaY < 0){
                this.increaseScale();
            } else {
                this.reduceScale();
            }
        }
    }


    increaseScale = () => {
        //@ts-ignore
        this.setState((state, props) => {
            if(state.scale + 0.2 >= 3){
                return {scale: state.scale}
            }
            return {scale: state.scale + 0.2}
        })
    }

    reduceScale = () => {
        //@ts-ignore
        this.setState((state, props) => {
            if(state.scale - 0.2 <= 0){
                return {scale: state.scale}
            }
            return {scale: state.scale - 0.2}
        }, () => {
            this.render();
        })
    }


    printDocument = async () => {
        //@ts-ignore
        const data = await this.state.pdf!.getData();
        const blob = new Blob([data], {type: 'application/pdf'});
        printJS(URL.createObjectURL(blob))
    }

    downloadDocument = () => {
        const requestOptions = {
            method: 'GET',
            headers: {
                'Authorization': `${localStorage.getItem('token')}`
            }
        };

        //@ts-ignore
        fetch(this.props.link, requestOptions)
            .then(response => response.blob())
            .then(blob => {
                const url = window.URL.createObjectURL(blob);
                const a = document.createElement('a');
                a.style.display = 'none';
                a.href = url;
                //@ts-ignore
                a.download = this.props.link.replace(`${process.env.REACT_APP_BACK_HOST}/media/pdf/`, '');
                document.body.appendChild(a);
                a.click();
                window.URL.revokeObjectURL(url);
            })
            .catch(error => console.log('error', error));
    }

    loadingDocument = () => {
        return(
            <div className="loadingPage">
                <div className="loadingPage__div">
                    <ArrowRepeat />
                    <p>Loading</p>
                </div>
            </div>
        )
    }

    render() {
        const {pdf, scale, isRender, isLoadingError} = this.state;

        if(!pdf && !isLoadingError){
            return (
                this.loadingDocument()
            )
        } else if(!pdf && isLoadingError){
            return (
                <div>
                        .     
                </div>
            )
        } else {
            return(
                <div>
                    <div className="pdfViewerTools" style={{position: 'fixed'}}>
                        <button
                            // shape="circle"
                            // icon={<ZoomInOutlined/>}
                            onClick={() => this.increaseScale()}
                            className={"panelButton"}
                        >
                            <PlusIcon />
                        </button>
                        <button
                            // shape="circle"
                            // icon={<ZoomOutOutlined/>}
                            onClick={() => this.reduceScale()}
                            className={"panelButton"}
                        >
                            <MinusIcon />
                        </button>
                        <button
                            // shape="circle"
                            // icon={<PrintIcon/>}
                            onClick={() => this.printDocument()}
                            className={"panelButton"}
                        >
                            <PrintIcon />
                        </button>
                        <button
                            // shape="circle"
                            // icon={<Icons/>}
                            onClick={() => this.downloadDocument()}
                            className={"panelButton"}
                        >
                            <Icons />
                        </button>
                        <div className="scale">
                            {Math.round(scale * 33)}%/100%
                        </div>
                    </div>
                    {!isRender &&
                    <div ref={this.mainDiv} style={{minHeight: '100vh'}}>
                        {this.state.numPagesArr.map((i) => (
                            <div style={{marginBottom: 20}}>
                                <PdfViewer
                                    key={i}
                                    page={i}
                                    pdf={this.state.pdf}
                                    scale={scale}
                                />
                            </div>
                        ))}
                    </div>
                    }
                </div>

            )
        }

    }
}
